<template>
	<view class="tui-rate-box" @touchmove="touchMove">
		<block v-for="(item, index) in numbers" :key="index">
			<view class="tui-icon"
				:class="['tui-relative','tui-icon-collection' + (hollow && (current <= index || (disabled && current <= index + 1)) ? '' : '-fill')]"
				:data-index="index" @tap="handleTap"
				:style="{ fontSize: size + 'px', color: current > index + 1 || (!disabled && current > index) ? getActiveColor : normal }">
				<view class="tui-icon" :class="['tui-icon-main','tui-icon-collection-fill']"
					v-if="disabled && current == index + 1"
					:style="{ fontSize: size + 'px', color: getActiveColor, width: percent + '%' }"></view>
			</view>
		</block>
	</view>
</template>

<script>
	export default {
		name: 'tuiRate',
		emits: ['change'],
		props: {
			//数量
			quantity: {
				type: [Number, String],
				default: 5
			},
			//当前选中
			current: {
				type: Number,
				default: 0
			},
			//当前选中星星分数(大于0，小于等于1的数)
			score: {
				type: [Number, String],
				default: 1
			},
			//禁用点击
			disabled: {
				type: Boolean,
				default: false
			},
			//大小
			size: {
				type: Number,
				default: 20
			},
			//未选中颜色
			normal: {
				type: String,
				default: '#b2b2b2'
			},
			//选中颜色
			active: {
				type: String,
				default: ''
			},
			//未选中是否为空心
			hollow: {
				type: Boolean,
				default: false
			},
			//自定义参数
			params: {
				type: [Number, String],
				default: 0
			}
		},
		computed: {
			getActiveColor() {
				return this.active || (uni && uni.$tui && uni.$tui.color.danger) || '#EB0909';
			}
		},
		data() {
			return {
				pageX: 0,
				percent: 0,
				numbers: []
			};
		},
		created() {
			this.handleQuantity(this.quantity)
			this.percent = Number(this.score || 0) * 100;
		},
		watch: {
			score(newVal, oldVal) {
				this.percent = Number(newVal || 0) * 100;
			},
			quantity(val) {
				this.handleQuantity(val)
			}
		},
		methods: {
			handleTap(e) {
				if (this.disabled) {
					return;
				}
				const index = e.currentTarget.dataset.index;
				this.$emit('change', {
					index: Number(index) + 1,
					params: this.params
				});
			},
			touchMove(e) {
				if (this.disabled) {
					return;
				}
				if (!e.changedTouches[0]) {
					return;
				}
				const movePageX = e.changedTouches[0].pageX;
				const distance = movePageX - this.pageX;

				if (distance <= 0) {
					return;
				}
				let index = Math.ceil(distance / this.size);
				index = index > this.quantity ? this.quantity : index;
				this.$emit('change', {
					index: index,
					params: this.params
				});
			},
			handleQuantity(quantity) {
				quantity = Number(quantity) || 5
				quantity = Math.ceil(quantity)
				this.numbers = Array.from(new Array(quantity + 1).keys()).slice(1)
			}
		},
		mounted() {
			this.$nextTick(()=>{
				setTimeout(() => {
					const className = '.tui-rate-box';
					let query = uni.createSelectorQuery().in(this);
					query
						.select(className)
						.boundingClientRect(res => {
							this.pageX = res.left || 0;
						})
						.exec();
				}, 80)
			})
		}
	};
</script>

<style scoped>
	@font-face {
		font-family: 'rateFont';
		src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
		font-weight: normal;
		font-style: normal;
	}

	.tui-icon {
		font-family: 'rateFont' !important;
		font-style: normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		display: block;
	}

	.tui-relative {
		position: relative;
	}

	.tui-icon-main {
		position: absolute;
		height: 100%;
		left: 0;
		top: 0;
		overflow: hidden;
	}

	.tui-icon-collection-fill:before {
		content: '\e6ea';
	}

	.tui-icon-collection:before {
		content: '\e6eb';
	}

	.tui-rate-box {
		display: inline-flex;
		align-items: center;
		margin: 0;
		padding: 0;
	}
</style>